/**
 * 
 * lodash
 * @param {function} func 
 * @param {number} await 
 * @param {object} options
 * @param {boolean} options.leading false 
 * @param {boolean} options.trailing true 
 * @param {number} options.maxWait debounce
 */
/* eslint-disable */
import { isObject } from './types';
import { assertFunction } from './asserts';

export default function debounce(func, wait, options) {
  const nativeMax = Math.max;
  const nativeMin = Math.min;
  function toNumber(arg) {
    return Number(arg);
  }
  function now() {
    return Date.now();
  }

  let lastArgs;
  let lastThis;
  let maxWait;
  let result;
  let timerId;
  let lastCallTime;
  // func 
  let lastInvokeTime = 0;
  // 
  let leading = false;
  let maxing = false;
  // 
  let trailing = true;

  assertFunction(func);

  // 
  wait = toNumber(wait) || 0;

  // options
  if (isObject(options)) {
    leading = !!options.leading;
    maxing = 'maxWait' in options;
    maxWait = maxing ? nativeMax(toNumber(options.maxWait) || 0, wait) : maxWait;
    trailing = 'trailing' in options ? !!options.trailing : trailing;
  }

  // 
  function invokeFunc(time) {
    const args = lastArgs;
    const thisArg = lastThis;
    lastArgs = lastThis = undefined;
    lastInvokeTime = time;
    result = func.apply(thisArg, args);
    return result;
  }

  // leading edge
  function leadingEdge(time) {
    // Reset any `maxWait` timer.
    lastInvokeTime = time;
    //  trailing edge 
    timerId = setTimeout(timerExpired, wait);
    // leading = true 
    return leading ? invokeFunc(time) : result;
  }

  // 
  function remainingWait(time) {
    // debounced
    const timeSinceLastCall = time - lastCallTime;
    // 
    const timeSinceLastInvoke = time - lastInvokeTime;
    //  wait  timeSinceLastCall trailing
    const result = wait - timeSinceLastCall;
    // 
    // maxing: maxingtrailing
    // maxing: trailingtimerExpired
    return maxing ? nativeMin(result, maxWait - timeSinceLastInvoke) : result;
  }

  //  func 
  function shouldInvoke(time) {
    const timeSinceLastCall = time - lastCallTime;
    const timeSinceLastInvoke = time - lastInvokeTime;
    // 
    // 
    return (lastCallTime === undefined
      //  wait
      || (timeSinceLastCall >= wait)
      // 
      || (timeSinceLastCall < 0)
      //
      || (maxing && timeSinceLastInvoke >= maxWait));
  }

  //  trailing edge  trailingEdge
  function timerExpired() {
    const time = now();
    if (shouldInvoke(time)) {
      return trailingEdge(time);
    }
    // 
    timerId = setTimeout(timerExpired, remainingWait(time));
  }

  // trailing edge
  function trailingEdge(time) {
    timerId = undefined;
    // lastArgs
    //  func  debounced  trailing edge 
    if (trailing && lastArgs) {
      return invokeFunc(time);
    }
    //  trailingEdge  lastArgs  lastThis
    //  trailing edge trailing edge 
    lastArgs = lastThis = undefined;
    return result;
  }

  // cancel
  function cancel() {
    if (timerId !== undefined) {
      clearTimeout(timerId);
    }
    lastInvokeTime = 0;
    lastArgs = lastCallTime = lastThis = timerId = undefined;
  }

  // flush--
  function flush() {
    return timerId === undefined ? result : trailingEdge(now());
  }

  function debounced() {
    const time = now();
    //
    const isInvoking = shouldInvoke(time);
    lastArgs = arguments;
    lastThis = this;
    lastCallTime = time; //
    // timerId
    // 1.
    // 2.trailingEdge
    if (isInvoking) {
      if (timerId === undefined) {
        return leadingEdge(lastCallTime);
      }
      if (maxing) {
        // Handle invocations in a tight loop.
        timerId = setTimeout(timerExpired, wait);
        return invokeFunc(lastCallTime);
      }
    }
    // casetrailing  true  wait  trailingEdge 
    //  shouldInvoke  trailingEdge 
    if (timerId === undefined) {
      timerId = setTimeout(timerExpired, wait);
    }
    return result;
  }

  debounced.cancel = cancel;
  debounced.flush = flush;
  return debounced;
}
