export const dictionary = {
  'zh-cn': {},
  'en-us': {
    '': 'Untitled',
    '': 'Cancel',
    '': 'Confirm',
    '': 'Restore',
    '': 'Maximize',
    '': 'Close',
    '': 'Loading',
  },
};

export default function i18n (word, lang = 'zh-cn', dic = {}) {
  return dic[word] || (dictionary[lang] || {})[word] || word;
}
